import React, { Component } from 'react'
import {connect} from 'react-redux'

class Login extends Component {
    login = () => {
        alert("hello");
        var values = {
            username: this.refs.username.value,
            password: this.refs.password.value
        }

        var options = {
            method: 'POST', // or 'PUT'
            body: JSON.stringify(values), // data can be `string` or {object}!
            headers: {
                'Content-Type': 'application/json'
            }
        }

        fetch('http://localhost:8000/login', options)
            .then(res => res.json())
            .then(data => {
                console.log('Success:', JSON.stringify(data))
                this.props.dispatch({type:'addUser', payload:data})
                this.props.history.push('/')
            })
            .catch(error => console.error('Error:', error));

    }

    isAthenticated = () => {
        alert("hello");
        var values = {
            username: this.refs.username.value,
            password: this.refs.password.value
        }

        var options = {
            method: 'POST', // or 'PUT'
            body: JSON.stringify(values), // data can be `string` or {object}!
            headers: {
                'Content-Type': 'application/json'
            }
        }

        fetch('http://localhost:8000/dashboard', options)
            .then(res => res.json())
            .then(response => {
                console.log('Success:', JSON.stringify(response))
                
            })
            .catch(error => console.error('Error:', error));

    }

    render() {
        return (
            <div className="main-login">
    {console.log(this.props)}
                <form>
                    <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Email address</label>
                        <input type="text" name="username" ref='username' className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" />
                        
                    </div>
                    <div className="form-group">
                        <label htmlFor="exampleInputPassword1">Password</label>
                        <input type="password" name="password" ref="password" className="form-control" id="exampleInputPassword1" placeholder="Password" />
                    </div>
                                        
                </form>
                <button onClick={this.login} className="btn btn-primary">Submit</button>
                <button onClick={this.isAthenticated} className="btn btn-primary">isAthenticted</button>
                {console.log(this.props.user)}
            </div>
        );
    }
}
var mapStateToProps=(store)=>{
    return {
        user:store.userReducer
    }
}
export default connect(mapStateToProps)(Login);