import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import { connect } from 'react-redux'
import { insertads, newSearch } from '../actions/action'
import { withRouter } from "react-router-dom";


class Nav extends Component {
    constructor(props) {
        super(props);
        this.state = {
            searchTitle: "",
            location: ""
        };
        this.handleSearch = this.handleSearch.bind(this);
        this.handleSearchReq = this.handleSearchReq.bind(this);
        // this.handleSearchLocation = this.handleSearchLocation.bind(this);
    }

    handleSearchReq = (e) => {
        this.setState({ searchTitle: e.target.value });
    }
    handleSearchLocation = (e) => {
        this.setState({ location: e.target.value });

    }


    search = () => {


            var obj = {
                title: this.state.searchTitle,
                location: this.state.location,
                catagory: this.props.search.catagory,
                brand: this.props.search.brand,
                pricefrom: this.props.search.pricefrom,
                priceto: this.props.search.priceto
            }
            this.props.dispatch(newSearch(obj))

            fetch("http://localhost:8000/search?title=" + obj.title + "&location=" + obj.location + "&catagory=" + obj.catagory + "&brand=" + obj.brand + "&pricefrom=" + obj.pricefrom + "&priceto=" + obj.priceto)
                .then((res) => res.text())
                .then((data) => {
                    console.log(data)
                    this.props.dispatch(insertads(JSON.parse(data)))
                })
                .catch((err) => console.log(err))

            this.props.history.push('/search')

      

    }

    handleSearch(e) {
        if (e.keyCode === 13 ) {

            var obj = {
                title: this.state.searchTitle,
                location: this.state.location,
                catagory: this.props.search.catagory,
                brand: this.props.search.brand,
                pricefrom: this.props.search.pricefrom,
                priceto: this.props.search.priceto
            }
            this.props.dispatch(newSearch(obj))


            fetch("http://localhost:8000/search?title=" + obj.title + "&location=" + obj.location + "&catagory=" + obj.catagory + "&brand=" + obj.brand + "&pricefrom=" + obj.pricefrom + "&priceto=" + obj.priceto)
                .then((res) => res.text())
                .then((data) => {
                    console.log(data)
                    this.props.dispatch(insertads(JSON.parse(data)))
                })
                .catch((err) => console.log(err))

            this.props.history.push('/search')





            // .then(()=>{console.log(this.props)})
            // const history = createBrowserHistory({forceRefresh: false});
            // history.location.push('/search');
            // const location = history.location;
            // location.pathname="/search"
            // console.log(location)
            // const { history } = this.props;
            // this.props.history.push('/')
            // console.log(this.props)
            // fetch("http://localhost:8000/search?title=" + this.state.searchTitle+"&location="+this.state.location )
            // .then((res) => res.text())
            // .then((data) => {
            //    console.log(data)

            //     this.props.dispatch(insertads(JSON.parse(data)))
            // })
            // .catch((err) => console.log(err))
        }
    }


    render() {
      
        return (

            <div >


                <nav className="navbar navbar-expand-lg navbar-dark  bg-dark">
                    <Link className="navbar-brand" to="/home">Ebuy</Link>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">

                        <form className="form-inline my-2 my-lg-0">
                            <input className="form-control mr-sm-2" type="search" placeholder="Location" aria-label="Search"
                                onKeyUp={this.handleSearch} onChange={this.handleSearchLocation} />
                            <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search"
                                onKeyUp={this.handleSearch} onChange={this.handleSearchReq} />
                            <button className="btn btn-secondary  my-2 my-sm-0" onClick={this.search} type="button">Search</button>
                        </form>


                        {
                            this.props.user === null ?

                                <ul className="navbar-nav nav-ul">
                                    <li className="nav-item">
                                        <Link className="nav-link" to="/login">Login</Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link className="nav-link" to="/signup">Sign Up</Link>
                                    </li>
                                </ul>
                                :
                                <ul className="navbar-nav nav-ul">
                                    <li className="nav-item">
                                        <Link className="nav-link" to="/sell">Sell </Link>
                                    </li>
                               
                                    <li className="nav-item">
                                        <Link className="nav-link" to="/userads">My Ads </Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link className="nav-link" to="/profile">Profile</Link>
                                    </li>
                                </ul>
                        }
                        {/* <ul className="navbar-nav nav-ul">
                                        <li className="nav-item">
                                            <Link className="nav-link" to="/sell">Sell </Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link" to="/chat">Profile</Link>
                                        </li>
                                        {
                                            console.log(this.props.user)
                                        }
                                        <li className="nav-item">
                                            <Link className="nav-link" to="/login">Login</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link" to="/signup">Sign Up</Link>
                                        </li>
                                    </ul> */}
                    </div>
                </nav>








            </div>
        );
    }
}


const mapStateToProps = (store) => {
    return {
        user: store.userReducer,
        search: store.searchReducer
    }
}
export default connect(mapStateToProps)(withRouter(Nav));